<template>
  <div class="select-theme">
      <el-form size="mini"  label-width="120px">
           <el-form-item label="">
              <el-color-picker
                size="small"
                v-model="mytheme.navigationBarBackgroundColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>

           <el-form-item label="">
                  <el-radio v-model="mytheme.navigationBarFrontColor" label="#000000"></el-radio>
                  <el-radio v-model="mytheme.navigationBarFrontColor" label="#ffffff"></el-radio>
           </el-form-item>

            <el-form-item label="">
              <el-color-picker
                size="small"
                v-model="mytheme.mainColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
            <el-form-item label="">
              <el-color-picker
                size="small"
                v-model="mytheme.secondaryColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
          <el-form-item label="">
              <el-color-picker
                size="small"
                v-model="mytheme.tipsColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
          <el-form-item label="">
              <el-color-picker
                size="small"
                v-model="mytheme.statusColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>

           <el-form-item label="">
              <el-color-picker
                size="small"
                v-model="mytheme.bgColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
      </el-form> 
  </div>
</template>

<script>
import  *  as style from '../style.js';
export default {
  name: 'HelloWorld',
  props: ['theme'],
  data(){
      return {
          predefineColors: JSON.parse(JSON.stringify(style.predefineColors)),
          mytheme:JSON.parse(JSON.stringify(style.theme)),
      };
  },
  watch:{
      theme:{
            handler(newObj,oldObj) {
                if(this.mytheme != this.theme){
                    this.mytheme = this.theme;
                }
            },
            immediate: true,
            deep: true
      },
      mytheme:{
            handler(newObj,oldObj) {
                if(this.mytheme != this.theme){
                     this.$emit('input',this.mytheme);
                }
            },
            immediate: true,
            deep: true
      }
  },
  created(){
     
  },
  methods:{
      
  },
}
</script>

<style scoped>

</style>
